<template>
    <div>
        <h4>Cliente</h4>
        <div class="row">
            <div class="col-xs-4" :class="{'has-error':error}">
                <div class="input-group form-inline">
                    <div class="input-group-btn dropdown-menu-left" >
                        <button type="button" class="btn btn-primary dropdown-toggle" id="cliente" data-toggle="dropdown" aria-expanded="true">
                            <span class="fa fa-caret-down"></span></button>
                        <ul class="dropdown-menu">
                            <li @click="seleccionarCliente(m)" v-for="(m) in clientes" :key="m.id">
                                <a>
                                    <b>{{m.nombre}}</b><br>
                                    <span class="text-muted">{{m.direccion}}</span>
                                </a>
                            </li>
                            <li v-if="clientes.length==0">
                                <a href="#">No hay clientes disponibles</a>
                            </li>
                        </ul>
                    </div>
                    <input @keyup.enter="buscarCliente" v-model="cliente.nombre" v-uppercase type="search" name="cliente" class="form-control"
                           placeholder="Cliente. Escriba para buscar" autocomplete="false" autofocus>
                </div>
                <span class="help-block">{{error}}</span>
            </div>
            <div class="col-xs-3">
                <input v-model="cliente.direccion" readonly name="direccion" class="form-control disabled" placeholder="Direccion">
            </div>
            <div class="col-lg-2">
                <input v-model="cliente.telefono" readonly name="telefono" class="form-control disabled" placeholder="Telefono">
            </div>
            <div class="col-xs-2">
                <input v-model="cliente.ocupacion" readonly name="ocupacion" class="form-control disabled" placeholder="Ocupacion">
            </div>
            <button @click="showUsersModal" :class="cliente.id>0?'bg-purple':'btn-primary'" class="btn" title="Agregar usuario">
                <i v-if="cliente.id>0" class="fa fa-pencil"></i>
                <i v-else class="fa fa-user-plus"></i>
            </button>
        </div>
        <div class="row">
            <div class="col-xs-10">
                <div class="h4">Productos</div>
            </div>
            <div class="col-xs-2 text-right">
                <div class="form-group text-red">
                    <label>Modo precios..</label>
                    <select v-model="comprobante.modo" :disabled="detalle.length>0 || producto.id>0" class="form-control">
                        <option value="normal">Normal</option>
                        <option value="urg">Urgencias</option>
                        <option value="m1">Maquila 1</option>
                        <option value="m2">Maquila 2</option>
                    </select>
                </div>
            </div>
        </div>
        <div class="row">
            <div class="col-xs-8">
                <div class="input-group">
                    <div class="input-group-btn dropdown-menu-left">
                        <button type="button" class="btn btn-primary dropdown-toggle" id="producto" data-toggle="dropdown" aria-expanded="true">
                            <span class="fa fa-caret-down"></span></button>
                        <ul class="dropdown-menu">
                            <li @click="seleccionarProducto(m)" v-for="(m) in productos" :key="m.id">
                                <a>
                                    <b>{{m.nombre}}</b><br>
                                </a>
                            </li>
                            <li v-if="productos.length==0">
                                <a href="#">No hay productos disponibles</a>
                            </li>
                        </ul>
                    </div>
                    <input @keyup.enter="buscarProducto" v-model="producto.nombre" v-uppercase type="search" class="form-control"
                           placeholder="Producto. Escriba para buscar" autocomplete="false" autofocus>
                </div>
            </div>
            <div class="col-xs-3">
                <div class="input-group">
                    <span class="input-group-addon">Precio $</span>
                    <div class="form-control disabled">{{producto.precio==null?'0':producto.precio}}</div>
                </div>
            </div>
            <div class="col-xs-1">
                <button autofocus @click="agregar" v-if="!cobrado" class="btn btn-primary form-control" >
                    <span class="fa fa-plus"></span>
                </button>
            </div>
        </div>
        <hr>
        <table class="table table-striped">
            <thead>
                <tr>
                    <th>Producto</th>
                    <th class="col-xs-3">Precio</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(m,i) in detalle" :key="i">
                    <td>
                        <div class="input-group" v-if="!cobrado">
                            <span class="input-group-btn">
                                <button v-if="!cobrado" class="btn btn-danger" @click="retirar(i)">
                                    <i class="fa fa-minus"></i>
                                </button>
                            </span>
                            <div class="form-control">{{m.nombre}}</div>
                        </div>
                        <div v-else class="form-control">{{m.nombre}}</div>
                    </td>
                    <td>
                        <div class="input-group">
                            <span class="input-group-addon">$</span>
                            <div class="form-control">{{m.precio}}</div>
                        </div>
                    </td>
                </tr>
            </tbody>
            <tfoot>
                <tr>
                    <td class="text-right">Importe</td>
                    <td><b> $ {{calcularSubtotal.toFixed(2)}}</b></td>
                </tr>
                <tr>
                    <td class="text-right">Descuento</td>
                    <td>
                        <div class="form-inline">
                            <div class="input-group col-xs-6">
                                <span class="input-group-addon">$</span>
                                <input :disabled="detalle.length==0 || this.$refs.porcentaje.value!=''" ref="monto" @keyup.enter="calcPorMonto" class="form-control">
                            </div>
                            <div class="input-group col-xs-6">
                                <span class="input-group-addon">%</span>
                                <input :disabled="detalle.length==0  || this.$refs.monto.value!=''" ref="porcentaje" @keyup.enter="calcPorPorcentaje" class="form-control">
                            </div>
                        </div>
                    </td>
                </tr>
                <tr>
                    <td class="text-right">Sub Total</td>
                    <td><b> $ {{calcularImporteConDescuento.toFixed(2)}}</b></td>
                </tr>
                <tr>
                    <td class="text-right"> IVA (16%)</td>
                    <td><b> $ {{calcularIva.toFixed(2)}}</b></td>
                </tr>
                <tr>
                    <td class="text-right">Total</td>
                    <td><b> $ {{calcularTotal.toFixed(2)}}</b></td>
                </tr>
                <tr v-if="detalle.length">
                    <td>
                        <button :disabled="!cobrado" @click="nuevaVenta" class="btn btn-app bg-red btn-lg pull-right">
                            <span class="fa fa-file"></span> Nueva Venta
                        </button>
                    </td>
                    <td>
                        <div class="">
                            <button :disabled="!cobrado" @click="imprimir" class="btn btn-app bg-green btn-lg">
                                <span class="fa fa-print"></span> Imprimir
                            </button>
                            <button :disabled="cobrado" @click="cobrar" class="btn btn-app bg-blue btn-lg pull-right">
                                <span class="fa fa-money"></span> Cobrar
                            </button>
                        </div>
                        
                    </td>
                </tr>
            </tfoot>
        </table>
        <!--agregar nuevo cliente-->
        <t-clientes :title="titulomodal" :model="cliente" @store="seleccionarCliente"/>
    </div>
</template>

<script>
    import tClientes from '@/components/templates/t-clientes'
    import api from '@/services'
    import Formulas from '@/lib/Formulas'
    import session from '@/lib/session'

    export default {
        name: "caja-rapida",
        components:{tClientes},
        mounted(){
            this.$emit('title','CAJA');
            this.comprobante.usuario = session.getKey('session_lab').user_name;
        },
        data(){
            return {
                error:null,
                cliente:{
                    id:0,
                    nombre:null,
                    telefono:null,
                    direccion:null,
                    ocupacion:null,
                    edad:null,
                    sexo:null,
                    fecha_nac:null
                },
                clientes:[],//para la busqueda de clientes
                producto:{ //producto seleccionado
                    id:0,
                    nombre:null,
                    precio:null
                },
                productos:[], //para la busqueda de productos
                comprobante:{ //los datos de la venta actual
                    id:0,
                    client_id: null,
                    modo:'normal',
                    descuento: 0,
                    usuario:null,
                    productos:[]
                },
                detalle:[], // listado de productos seleccionados por el cliente
                cobrado:false,
                importeDescuento:0,
                url_id: '',
                titulomodal: '',
            }
        },
        methods: {
            buscarCliente(){
                api.clientes.ventas(this.cliente).then((r)=>{
                    if (r.data.length) this.clientes = r.data;
                    else this.clientes =  [];
                    window.$('#cliente').dropdown('toggle');
                });
            },
            buscarProducto(){
                api.productos.ventas(this.producto).then((r)=>{
                    if (r.data.length) this.productos = r.data;
                    else this.productos =  [];
                    window.$('#producto').dropdown('toggle');
                });
            },
            seleccionarCliente(m){ //para seleccionar el cliente y modificarlo si se desea
                this.comprobante.client_id = m.id;
                this.cliente.id = m.id;
                this.cliente.nombre = m.nombre;
                this.cliente.telefono = m.telefono;
                this.cliente.direccion = m.direccion;
                this.cliente.ocupacion = m.ocupacion;
                this.cliente.edad = m.edad;
                this.cliente.sexo = m.sexo;
                this.cliente.fecha_nac = m.fecha_nac;
            },
            seleccionarProducto(m){ //para elegir el producto de la lista
                this.producto.id = m.id;
                this.producto.nombre = m.nombre;
                switch(this.comprobante.modo){
                    case 'normal':
                        this.producto.precio = m.precio;
                        break;
                    case 'urg':
                        this.producto.precio = m.precio_urg;
                        break;
                    case 'm1':
                        this.producto.precio = m.precio_m1;
                        break;
                    case 'm2':
                        this.producto.precio = m.precio_m2;
                        break;
                }
            },
            agregar(){
                let arr = {
                  producto_id: this.producto.id,
                  nombre: this.producto.nombre,
                  precio: this.producto.precio
                };

                if (arr.producto_id >0){
                  this.detalle.unshift(arr);
                  this.comprobante.productos.unshift({
                      producto_id: this.producto.id
                  });
                  this.limpiarProducto();
                }
              return;
            },
            retirar(i){ this.detalle.splice(i,1);},
            limpiarProducto(){
                this.producto.id = 0;
                this.producto.nombre = null;
                this.producto.precio = null;
                this.producto.total = 0;
            },
            cobrar(){
                this.error = null;
                if (!this.cobrado){
                    api.ventas.save(this.comprobante).then((r)=>{
                        if (r.status == 200){
                            this.$toast.success({msj:'Venta generada con éxito.'});
                            this.$toast.info({msj:'Generando ticket de pago.'});
                            this.cobrado = true;
                            this.url_id = r.data.data.uuid; //el uuid del reporte pdf
                        }
                    }).catch((e)=>{
                        if (e.response.status === 422){
                            this.error = e.response.data.errors.client_id[0];
                            this.$toast.catch({msj:e.response.data.errors.client_id[0]});
                        }
                    });
                }
                
            },
            imprimir(){
                let token = session.getKey('session_lab').api_token;
                window.open(`${window.base_url}/ticket/pdf/${this.url_id}?api_token=${token}`)
            },
            clearCliente(){
                this.cliente.id = 0;
                this.cliente.nombre = null;
                this.cliente.telefono = null;
                this.cliente.direccion = null;
                this.cliente.ocupacion = null;
                this.cliente.edad = null;
                this.cliente.sexo = null;
                this.cliente.fecha_nac = null;
            },
            nuevaVenta(){
                if (this.cobrado){
                    this.importeDescuento = 0;
                    this.$refs.monto.value = '';
                    this.$refs.porcentaje.value = '';
                    this.clearCliente();
                    this.limpiarProducto();
                    this.detalle = [];
                    this.comprobante.id  = 0;
                    this.comprobante.productos  = [];
                    this.cobrado = false;
                }
            },
            showUsersModal(){
                window.$('#modal').modal({
                    backdrop:'static',
                    show:true
                });
                this.titulomodal = this.cliente.id>0?'Actualizar Cliente':'Registrar Nuevo Cliente';
            },
            //calcular el descuento
            calcPorPorcentaje(){
                let desc = parseFloat(this.$refs.porcentaje.value);
                this.importeDescuento = this.calcularSubtotal * (desc/100);
                this.comprobante.descuento = this.importeDescuento;
            },
            calcPorMonto(){
                this.importeDescuento = parseFloat(this.$refs.monto.value);
                this.comprobante.descuento = this.importeDescuento;
            },

        },
        computed: {
            calcularSubtotal(){
                return Formulas.calcularSubTotal(this.detalle);
            },
            calcularIva(){
                if (this.importeDescuento>0) {
                    return Formulas.calcularIva(this.calcularImporteConDescuento);
                }else 
                return Formulas.calcularIva(this.calcularSubtotal);
            },
            calcularTotal(){
                if (this.importeDescuento>0) {
                    return Formulas.calcularTotal(this.calcularImporteConDescuento ,this.calcularIva);
                }else 
                return Formulas.calcularTotal(this.calcularSubtotal ,this.calcularIva);
            },
            calcularImporteConDescuento(){
                return Formulas.calcularImporteConDescuento(this.calcularSubtotal,this.importeDescuento);
            }
        }
    }
</script>