class Formulas{

    calcularDescuento(precio, descuento){
        return (precio * descuento);
    }

    calcularSubTotal(obj){
        let subTotal = 0;
        obj.forEach((x) => {
            subTotal = subTotal + x.precio;
        });
        return subTotal;
    }

    calcularImporteConDescuento(subtotal,descuento){
        if(descuento>0){
            return subtotal - descuento;
        }
        return 0;
        
    }
    
    calcularIva(importe){
        return importe * 0.16;
    }
    calcularTotal(subtotal,iva){
        return subtotal + iva;
    } 

    detalle(obj){
        obj.forEach((x)=>{
            delete x.nombre;
            delete x.precio;
            delete x.descuento;
            delete x.montodescuento;
            delete x.total;
        });
        return obj;
    }
}
export default Formulas.prototype