<template>
    <c-modal id-modal="modal" :title="model.id>0?'Editar cliente':'Registrar nuevo Cliente'">
        <template v-slot:body>
            <form role="form" @submit.prevent="onSubmit" @reset="onReset">
                <ValidationObserver ref="form">
                    <div class="box-body">
                        <div class="row">
                            <ValidationProvider vid="nombre" name="nombre" rules="required" v-slot="{errors}">
                                <div class="form-group col-md-8" :class="{'has-error':errors.length==1}">
                                    <label>Nombre</label>
                                    <input v-model="model.nombre" v-uppercase type="text" name="nombre" class="form-control" autofocus :required="!search" autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="telefono" name="telefono" rules="required|phone" v-slot="{errors}">
                                <div class="form-group col-md-4" :class="{'has-error':errors.length==1}">
                                    <label>Telefono</label>
                                    <input v-model="model.telefono"  type="text" name="telefono" class="form-control" maxlength="10" autofocus :required="!search" autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                        </div>
                        <ValidationProvider vid="direccion" name="direccion" rules="required" v-slot="{errors}">
                            <div class="form-group" :class="{'has-error':errors.length==1}">
                                <label>Dirección</label>
                                <input v-model="model.direccion" v-uppercase type="text" name="direccion" class="form-control" autofocus :required="!search" autocomplete="false">
                                <span class="help-block">{{errors[0]}}</span>
                            </div>
                        </ValidationProvider>
                        <div class="row">
                            <ValidationProvider vid="ocupacion" name="ocupacion" rules="required" v-slot="{errors}">
                                <div class="form-group col-md-4" :class="{'has-error':errors.length==1}">
                                    <label>Ocupación</label>
                                    <input  v-model="model.ocupacion" v-uppercase type="text" name="ocupacion" class="form-control" autofocus :required="!search" autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="edad" name="edad" rules="required|age" v-slot="{errors}">
                                <div class="form-group col-md-2" :class="{'has-error':errors.length==1}">
                                    <label>Edad</label>
                                    <input v-model.number="model.edad" type="number" name="edad" class="form-control" min="1" maxlength="2" autofocus :required="!search" autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="sexo" name="sexo" rules="required" v-slot="{errors}">
                                <div class="form-group col-md-3" :class="{'has-error':errors.length==1}">
                                    <label>Sexo</label>
                                    <div class="radio">
                                        <label>
                                            <input v-model="model.sexo" type="radio" name="sexo" value="h" autofocus> Hombre
                                        </label>
                                        <label>
                                            <input v-model="model.sexo" type="radio" name="sexo" value="m" autofocus> Mujer
                                        </label>
                                    </div>
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                            <ValidationProvider vid="fecha_nac" name="fecha" rules="date" v-slot="{errors}">
                                <div class="form-group col-md-3" :class="{'has-error':errors.length==1}">
                                    <label>Fecha de nacimiento</label>
                                    <input v-model="model.fecha_nac" type="date" name="fecha_nac" class="form-control" autofocus autocomplete="false">
                                    <span class="help-block">{{errors[0]}}</span>
                                </div>
                            </ValidationProvider>
                        </div>
                    </div>
                </ValidationObserver>
                <div class="box-footer">
                    <c-btn type="submit" color="btn btn-primary" icon="fa-check" :label="labelSubmit"/>
                    <c-btn data-dismiss="modal" aria-label="Close" type="reset" color="btn btn-danger pull-right" icon="fa-remove" :label="labelReset"/>
                </div>
            </form>
        </template>
    </c-modal>
</template>

<script>
    import api from '@/services'

    export default {
        name: "t-clientes",
        props:{
            loading:{type:Boolean,require:false,default:false},
            modelId:{
                type: Number,
                required: false,
                default: 0
            },
            model:{
                type:Object,
                required: false,
            },
            labelSubmit: {
                type:String,
                required: false,
                default: 'Guardar'
            },
            labelReset: {
                type:String,
                required: false,
                default: 'Cancelar'
            },
            search:{
                type:Boolean,
                required:false,
                default:false
            },
            title:{
                type:String,
                required:false,
                default: 'Registrar nuevo Cliente'
            }
        },
        methods:{
            onSubmit(){
                if (this.search){
                    this.filter();//buscar
                } else{
                    this.validateForm(); //validar y guardar
                }
            },
            validateForm(){
                this.$refs.form.validate().then((success)=>{
                    if (success){
                        //this.$emit('onSubmit',this.model);
                        if (this.model.id>0){
                            this.update();
                        } else this.store();
                    }
                });
            },
            filter(){
                this.$emit('search',this.model);
                this.closeModal();
            },
            onReset(){
                this.$refs.form.reset();
            },
            //methods api
            store(){
                api.clientes.save(this.model).then((r)=>{
                    if (r.status == 201){
                        this.$toast.success();
                        this.$emit('store',r.data.data);
                        this.closeModal();
                    }
                }).catch((err)=>{
                    if (err.response.status === 422){
                        this.$refs.form.setErrors(err.response.data.errors);
                        this.$toast.error();
                    }
                    if (err.response.status>=500){
                        this.$toast.catch();
                    }
                });
            },
            update(){
                api.clientes.update(this.model).then((r)=>{
                    if (r.status === 200){
                        this.$toast.info();
                        this.$emit('update',r.data.data);
                        this.closeModal();
                    }
                }).catch((err)=>{
                    if (err.response.status === 422){
                        this.$refs.form.setErrors(err.response.data.errors);
                        this.$toast.error();
                    }
                    if (err.response.status>=500){
                        this.$toast.catch();
                    }
                });
            },
            closeModal(){
                window.$('#modal').modal('hide');
            },
        }
    }
</script>